.Header{
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
}

.Header>h1{
    color: var(--font-color);
    font-size: 2rem;
}

.Header>p{
    color: var(--dark-font-color);
    font-size: 1rem;
}

.Header a{
    color: var(--font-color);
    text-decoration: underline;
}